'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function PackageDetailsPage() {
    const [selectedPackageType, setSelectedPackageType] = useState<'small' | 'large' | 'document' | 'other'>('small');
    const [notes, setNotes] = useState('');
    const [preferredDate, setPreferredDate] = useState('');

    const packageTypes = [
        { id: 'small', label: 'Small Parcel' },
        { id: 'large', label: 'Large Parcel' },
        { id: 'document', label: 'Document' },
        { id: 'other', label: 'Other' }
    ];

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                </div>
            </div>

            {/* Header */}
            <div className="text-center px-8 mb-8">
                <h1 className="text-4xl font-bold mb-2">Pickom</h1>
                <p className="text-gray-400 text-lg">People-Powered Delivery</p>

                <h2 className="text-3xl font-bold mt-12 mb-8">Select Package Type</h2>
            </div>

            {/* Package Type Selection */}
            <div className="flex-1 px-8 space-y-4">
                {packageTypes.map((type) => (
                    <button
                        key={type.id}
                        onClick={() => setSelectedPackageType(type.id as any)}
                        className={`w-full py-4 px-6 rounded-lg text-left text-lg font-medium transition-all ${selectedPackageType === type.id
                                ? 'bg-orange-500 text-white'
                                : 'bg-gray-800 text-white border border-gray-600'
                            }`}
                    >
                        {type.label}
                    </button>
                ))}

                {/* Preferred Date */}
                <div className="mt-8">
                    <label className="block text-white text-lg font-medium mb-4">
                        Preferred Pickup Date
                    </label>
                    <input
                        type="text"
                        value={preferredDate}
                        onChange={(e) => setPreferredDate(e.target.value)}
                        className="w-full bg-gray-800 border border-gray-600 rounded-lg px-4 py-4 text-white placeholder-gray-400 text-lg"
                        placeholder="Select date"
                    />
                </div>

                {/* Notes Section */}
                <div className="mt-8">
                    <h3 className="text-xl font-bold mb-4">Notes</h3>
                    <textarea
                        value={notes}
                        onChange={(e) => setNotes(e.target.value)}
                        className="w-full bg-gray-800 border border-gray-600 rounded-lg px-4 py-4 text-white placeholder-gray-400 text-lg h-24 resize-none"
                        placeholder="Leave a note..."
                    />
                </div>

                {/* Hand Pointer Icon */}
                <div className="flex justify-center mt-8">
                    <div className="relative">
                        <svg className="w-12 h-12 text-orange-500" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M9 11H7v2h2v-2zm4 0h-2v2h2v-2zm4 0h-2v2h2v-2zm2-7h-2V2c0-.55-.45-1-1-1s-1 .45-1 1v2H9V2c0-.55-.45-1-1-1s-1 .45-1 1v2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V6c0-1.1-.9-2-2-2zm0 16H5V9h14v11z" />
                        </svg>
                    </div>
                </div>
            </div>

            {/* Continue Button */}
            <div className="p-8">
                <Link
                    href="/ai-matching"
                    className="w-full bg-orange-500 text-white py-4 rounded-full text-lg font-medium text-center block hover:bg-orange-600 transition-colors"
                >
                    Continue
                </Link>
            </div>

            {/* Bottom Indicator */}
            <div className="flex justify-center pb-6">
                <div className="w-32 h-1 bg-white rounded-full"></div>
            </div>
        </div>
    );
} 